.Profile {
	padding: 6px 6px 52px 6px;
	width: 100%;
	text-align: center;
	width: 512px;
	@include componentBG;
	
	.DisplayUser {
		display: flex;
		max-width: 100%;

		.Banner {
			background: url('../../resources/UserBanner.jpg');
			border-radius: 12px 12px 0px 0px;
			position: absolute;
			height: 132px;
			width: 100%;
			max-width: 499px;
			margin: 0px auto;
		} // Banner

		.ProfilePic {
			height: 124px;
			width: 124px;
			padding: 0;
			border-radius: 50%;
			border: 4px white solid;
			margin: 64px auto 0px auto;
			z-index: 1;

		} // ProfilePic

	} // DisplayUser

	.UserInfo {

		h2 {
			margin: 1rem;
			line-height: 2rem;
			color: #3E3F40;

		} // h2 - name

		p {
			@include subText;
			line-height: 0;
			margin: 1rem 0rem 1.6rem 0rem;

		} //p - location

		h6 {
			color: white;
			background-color: #3C68FF;
			border-radius: 24px;
			width: 128px;
			height: 40px;
			line-height: 40px;
			margin: 0px auto;

		} // h6 - Level

	} // UserInfo

	.Stats {
		padding-top: 32px;
		display: flex;
		justify-content: space-around;

		.Box {
			width: 100%;

			p {
				@include subText;

			} // p

			h5 {
				color: #5B5C5F;
				font-size: 1.8rem;
				margin: 0;
				line-height: 0.25rem;;
			}

		} // Box

	} // Stats

} // Profile
